@use "../../assets/sass/_variables.scss" as *;


.filtered-item{
    height:65ch;
    display: flex;
    flex-direction: column;
    margin: 50px;
    &__input{
        display: flex;
        flex-direction: column;
        align-items: center;
    &-field{
        margin-right: 5px;
    }
    &-text{
        font-size: 1rem;
    }
    }
}

@media screen and (min-width: 768px){
   
.filtered-item{
    height:65ch;
    display: flex;
    flex-direction: column;
    margin: 50px;
    &__input{
        display: flex;
        align-items: center;
    &-field{
        margin-right: 20px;
    }
    &-text{
        font-size: 1.2rem;
    }
    }
}
}

