@use "../../assets/sass/_variables.scss" as *;

.beer{
    display: flex;
    flex-direction: column;
    width: 170px;
    height: 300px;
    margin: 40px;
    border-radius: 20px;
    line-height: 15px;
    background-color: $color-primary;
    border: 4px solid $color-tertiary;
    box-shadow: 10px 10px $color-tertiary;
    &__name{
        font-size: 1rem;
        text-align: center;
        border: 4px solid $color-tertiary;
        box-shadow: 5px 5px  $color-tertiary;
        border-radius: 20px;
    }
    &__info{
        font-size: .9rem;
    }
    &__data{
        font-size: .9rem;
    }
    &__collection{
        display: flex;
        flex-direction: column;
        align-items: center;
        
    line-height: 5px;
    }
}

.beer__img{
    display: flex;
    justify-content: center;
}

.beer__img-position{
    height: 100px;
    
}


@media screen and (min-width: 768px){
    .beer{
        width: 300px;
        height: 600px;
        padding: 40px;
        margin: 40px;
        border-radius: 20px;
        line-height: 25px;
        &__name{
            font-size: 1.7rem;
            padding: 5px;
            text-align: center;
            border: 4px solid $color-tertiary;
            box-shadow: 5px 5px  $color-tertiary;
            border-radius: 20px;
        }
        &__info{
            font-size: 1.3rem;
        }
        &__data{
            font-size: 1.1rem;
        }
    }
    
    .beer{
        background-color: $color-primary;
        border: 4px solid $color-tertiary;
        box-shadow: 10px 10px $color-tertiary;
    }
    
    .beer__img{
        display: flex;
        justify-content: center;
    }
    
    .beer__img-position{
        height: 230px;
    }
    
  }
  